export default class Effect {
  constructor(invoker, config = {}) {

    this.invoker = invoker;

    this.config = config;

    this.target = invoker.gameObject;

    this.startingRoutine = () => { };
    this.stoppingRoutine = () => { };
  }

  play() {
    if (this.invoker.name === 'EffectComponent') {
      this.startingRoutine();
    }
  }

  stop() {
    if (this.invoker.name === 'EffectComponent') {
      this.stoppingRoutine();
    }
  }
}
